import React from 'react';
import { default as MuiSkeleton, SkeletonProps } from '@mui/material/Skeleton';

const Skeleton = React.forwardRef<HTMLElement, SkeletonProps>( ( props, ref ) => {
	return (
		<MuiSkeleton
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SkeletonProps>;

export default Skeleton;
